import mkbUserItem from '../../components/mkb-user-item/mkb-user-item.vue';
import Messages from '../../util/messages.js';
const RongIMLib = require('../../lib/RongIMLib.wx-1.2.0.js');
import Constant from '../../util/constant.js';
import app from "../../App.vue"
import {
	axios
} from '../../api/api.js';
export default {
	components: {
		mkbUserItem
	},
	computed: {
		loginStatus() {
			return this.$store.state.loginStatus
		},
		userInfo() {
			return this.$store.state.userInfo
		}
	},
	data() {
		return {
			loginUser: {
				avatar: Constant.defaultHeadImg,
				items: [{
						src: "../../static/default/liu_lan.png",
						text: "浏览记录",
						url: "/pages/user/record",
						index: 0
					},
					{
						src: "../../static/default/shou_cang.png",
						text: "收藏记录",
						url: "/pages/user/record",
						index: 2
					},
					{
						src: "../../static/default/dian_zan.png",
						text: "点赞记录",
						url: "/pages/user/record",
						index: 1
					}
				],
				name: ''
			},
			avatarUrl: Constant.defaultHeadImg,
			nickName: "注册/登录",
			data: {
				appKey: 'bmdehs6pbguas',
				token: wx.getStorageSync('token'),
			}


		}
	},

	onShow() {

		if (!uni.getStorageSync("token")) {
			this.checkLogin();
		} else {
			this.getUserInfoAsyn();
		}

	},
	onLoad() {
		// 查看是否授权
		wx.getSetting({
			success(res) {
				if (res.authSetting['scope.userInfo']) {
					// 已经授权，可以直接调用 getUserInfo 获取头像昵称
					wx.getUserInfo({
						success: function(res) {
							console.log(res.userInfo)
						}
					})
				}
			}
		})
	},

	methods: {
		/**
		 * 获取缓存中的用户信息
		 */
		getUserInfoAsyn() {
			if (uni.getStorageSync("userInfo")) {
				let userInfo = uni.getStorageSync("userInfo");
				this.avatarUrl = userInfo.avatarUrl;
				this.nickName = userInfo.nickName;
			}
		},

		/**
		 * h5登录
		 */
		loginApp() {








		},

		/**
		 * 列表跳转
		 * @param {Object} e
		 */
		goUrl(e) {
			uni.navigateTo({
				animationType: 'pop-in',
				animationDuration: 200,
				url: `${e.url}?type=${e.type}`
			})

		},


		/**
		 * 微信用户授权
		 * @param {Object} e
		 */
		getUserInfo(e) {
			console.log(e)
			if (e.detail && e.detail.rawData) {
				this.wxUserLogin(e.detail);
			} else {
				uni.showModal({
					title: '提示',
					content: Messages.APPLY_AUTH,
					showCancel: false,
					confirmColor: '#fa5151'
				})
			}
		},

		/**
		 * 微信用户授权登录
		 * @param {Object} detail
		 */
		wxUserLogin(detail) {
			let that = this;
			uni.showLoading({
				title: Messages.AUTH_LOADING,
			});
			uni.login({
				provider: 'weixin',
				success: (loginRes) => {
					uni.hideLoading();
					if (loginRes && loginRes.code) {
						that.$store.commit("setRandCode",loginRes.code);
						let params = {
							code: loginRes.code,
							rawData: detail.rawData,
							signature: detail.signature,
							encryptedData: detail.encryptedData,
							iv: detail.iv
						};
						that.authLogin(params);
					}
				},
			})
		},
		
		
		/**
		 * 用户授权登录使用
		 * @param {Object} params
		 */
		authLogin(params){
			let that = this;
			that.$http.login(params).then(res => {
				if (res && res.code == 200) {
					let userInfo = {
						avatarUrl: res.data.avatarUrl,
						nickName: res.data.nickName,
						isSubscribe: res.data.isSubscribe,
						userId: res.data.userId
					};
					that.avatarUrl = userInfo.avatarUrl;
					that.nickName = userInfo.nickName;
					that.$store.commit('setToken', res.data.token);
					that.$store.commit('setUserInfo', userInfo);
					that.$store.commit('setLoginStatus', 1);
			
					that.initSDK(that);
					// console.log("重连")
					// app.globalData.Services.Status.reconnect();
			
				} else {
					uni.showToast({
						icon: "none",
						title: res.msg || Messages.FAIL_INFO
					});
				}
			}).catch(err => {
				uni.showToast({
					icon: "none",
					title: err.msg || Messages.FAIL_INFO
				});
			})
		},



		initSDK(context) {

			const self = this

			let appKey = context.data.appKey;
			let token = context.data.token;

			if (appKey == '' || token == '') {
				// return showPrompt('必须提供 appKey 和 token');
			}

			this.$http
				.imUserRegister({
					id: self.userInfo.userId,
					name: self.userInfo.nickName,
					portraitUri: self.userInfo.avatarUrl,
				})
				.then(res => {
					console.log(res)

					// 公有云初始化
					let modules = {
						//protobuf: 'https://cdn.ronghub.com/protobuf-2.3.1.min.js' //支持http(s)网络路径、本地相对路径,
						RongIMLib: RongIMLib,
						protobuf: null
					};

					let params = {
						appKey: appKey,
						token: token,
						connectToken: res.data.token
					};

					let callbacks = {
						receiveNewMessage: function(message) {
							uni.$emit('newMessage', message)
						},
						getCurrentUser: function(userInfo) {
							// wx.hideLoading();
						},
						onTokenIncorrect: function() {
						},
						watchConversationList: function(list) {
						}
					}
					app.globalData.Services.init(params, callbacks, modules);
				})
				.catch(err => {
					console.log(err)
				})
		},

		

		//退出
		logout() {
			let that = this;

			that.$http.logout({})
				.then(res => {
					if (res && res.code == 200) {
						uni.showToast({
							icon: "none",
							title: Messages.SUCCESS_LOGOUT
						});
						that.$store.commit('logout');

						app.globalData.Services.Status.disconnect();
					} else {
						uni.showToast({
							icon: 'none',
							title: res.msg || Messages.FAIL_LOGOUT
						})
					}
				})

			
		},

		/**
		 * 检查session 是否已经登录
		 */
		checkLogin() {
			let that = this;
			uni.showLoading({
				title: "加载中"
			});
			uni.login({
				provider: "weixin",
				success: (loginRes) => {
					uni.hideLoading();
					if (loginRes && loginRes.code) {
						that.$store.commit("setRandCode",loginRes.code);
						that.$http.getSessionToken({code: loginRes.code})
							.then(res => {
								if (res && res.code == 200 && res.data && res.data.token) {
									let userInfo = {
										avatarUrl: res.data.avatarUrl,
										nickName: res.data.nickName,
										isSubscribe: res.data.isSubscribe,
										userId: res.data.userId
									};
									that.avatarUrl = userInfo.avatarUrl;
									that.nickName = userInfo.nickName;
									that.$store.commit('setToken', res.data.token);
									that.$store.commit('setUserInfo', userInfo);
									that.$store.commit('setLoginStatus', 1);

									that.initSDK(that)

								} else {
									that.$store.commit('logout');
								}
							})
					}
				}
			})
		}

	}
}
